        text-align: center;
    }
    .bd_show .con{
        color:#999999;
        margin-left:3%;
        font-size:12px;
    }
    .bd_show img{
        margin-left:5%;
    }
    .bd_top>div{
        width:32%;
    }
    .bd_top img{
        width:70%;
    }
    .bd_top  p{
        font-size:12px;
        text-align:center;  
    }
    .bd_top .left{
        position:relative;
    }
    .bd_top .title{
        color:white;
    }
    .bd_top .left{
        background-color:white;
        float:left;
        overflow:hidden;
    }
    .bd_top>div:nth-of-type(1),.bd_top>div:nth-of-type(2){
        margin-right:2%;
    }
    .bd_top .left>div,.bd_top .right>div{
        position:relative;
        background:url("../../assets/images/background.png") no-repeat;
        background-position-y:8px;
        text-align:center;
        border-radius:10px;
    }
    .bd_top .middle_swipe div{
        background-color: #FF6600;
    }
    .bd_top .type{
        width:40%;
        background-color:white;
        margin-left:auto;
        margin-right:auto;
        border-radius:10px;
        border:1px solid #E8B58E;
        color:rgb(224, 168, 108);
    }
    .bd_top .con{
        color:#F5E0CF;
    }
    .bd .middle_swipe{
        width:32%;
        float: left;
        background-color:white;
    }
    .bd .middle_swipe div{
        position:relative;
        background:url("../../assets/images/db_middle11.png") no-repeat;
        background-position-y:8px;
        text-align:center;
        border-radius:10px;
    }
    .bd_top .right{
        float:right;
         background-color:white;
    }
    .bd_top>div{
        border-radius:10px;
    }
    .bd_top {
        margin-top:2%;
        overflow:hidden;
    }
    .bd_show{
        margin-top:2%;
    }
    .bd>p{
        font-size:18px;
        font-weight:bold;
        opacity:0.6;
    }
    .like{
        width:95%;
        margin-left:auto;
        margin-right:auto;
    }
    .like>p{
        width:100%;
    }
    .like>p img{
        width:100%;
    }
    .like ul{
        width:100%;
        overflow:hidden;

    }
    .like ul li{
        width:48%;
        float:left;
        margin-bottom:3%;
        background:white;
        border-radius:10px;
    }
    .like ul li:nth-of-type(2n){
        margin-left:3%; 
    }
    .like ul img{
        width:100%;
        border-radius:10px;
    }
    .like ul .title{
        display: -webkit-box;
        -webkit-box-orient: vertical;
        /*  */
        -webkit-line-clamp: 2;
        overflow: hidden;
        text-overflow: ellipsis;
        word-break: break-all;
        font-size:13px;
        font-weight:bold;
    }
    .like .lab>span{
        font-size:12px;
    }
    .like .lab span{
        border-radius:3px;
    }
    .like .lab span.ss{
        background-color:#ffcc00;
        color:black;
    }
    .like .lab span.action{ 
        background-color:#FBFEEE;
        border:1px solid #FFBB00;
    }
    .like .lab span{
        margin-right: 2%;
    }
    .like .lab span.fr{
        background-color:#7A51FA;
        color:white;
    }
    .like li p{
        margin-left:3%;
    }
    .like .title{
        height:34px;
    }
    .like .lab{
        height:34px;
        line-height:34px;
        vertical-align:center;
    }
    .like li>p{
        height:34px;
    }
    .like .bottom .price{
        color:red;
        font-size:12px;
    }
    .like .bottom i.price{
        font-size:18px;
        font-weight:bold;
        font-style:normal;
    }
    .like .bottom .eva{
        font-size:12px;
        color:rgb(153,153,153);
        margin-left:1%;
    }
    .like .bottom .adv{
        font-size:12px;
        margin-left:43%;
        color:rgba(153,153,153,0.3);
    }
    .foot_b{
        width:95%;
        margin-bottom:13%;
    }   
    .foot_b img{
        width:100%;
    }
    .like{
        margin-top:3%;
    }
    .header>p{
        width:100%;
        height:50px;
    }
    .header>p>span{
        float:left;
    }
    .header>p>b{
        float:left;
    }
    .header>p>i{
        float:right;
    }
    .header{
        width:100%;
        height:50px;
        background-color:rgb(255,219,71);
        display:none;
        height:50px;
        position:fixed;
        top:0px;
        left:0px;
        right:0px;
    }
    .header>p{
        width:95%;
        height:50px;
        margin-left: auto;
        margin-right: auto;
    }
    .header p span{
        width:5%;
        margin-top:3%;
        float: left;
    }
    .header p img{
        width: 100%;
    }
    .header p i{
        margin-top:3%;
        width:5%;
        float: right;
    }
    .header b{
        width:80%;
        height:36px;
        line-height:36px;
        background-color:white;
        margin-top:2%;
        margin-left:5%;
        border-radius:50px;
        position:relative;
    }
    .header b{
        font-weight:normal;
    }
    .header b input{
        margin-left:10%;
        outline:none;
        border:none;

    }
    .header b span{
        position:absolute;
        left:9px;
        top:-1px;
        width:20px;
        height:20px;
        background:url("../../assets/images/search.png") no-repeat;
        background-size:100%;
    }
</style>
<style>
    .index_swipe{
        width:94%;
        text-align:center;
        margin-left: auto;
        margin-right: auto;
        padding-left:3%;
        padding-right:3%;
    }
    .index_swipe img{
        width:100%;
        height: 100%;
        border-radius: 10px;
    }
    .index_swipe .mint-swipe-items-wrap{
        /* padding-bottom */
        /* padding-bottom:29.46%; */
        padding-bottom:49.11%;
        border-radius: 10px;
    }
</style>


